*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
}
.App{
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  width: 80%;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

header{
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;
}

main{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow:1;
          flex-grow:1;
}

aside{
  min-width: 230px;
  padding: 10px;
}

section{
  -webkit-flex-grow:1;
          flex-grow:1; 
}

a{
    text-decoration: none;
    color: #222;
}

li{
    list-style: none;
}

a{
    display: block;
    padding: 5px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;

    /* border-left: 5px solid transparent; */
}

a:hover{
    -webkit-transform: translate(10px);
            transform: translate(10px);
}

a.active{
    border-left: 5px solid red;
    -webkit-transform: translate(0);
            transform: translate(0);
}
